.Logo {
    align-items: center;
    border: solid 3px #f5c16b;
    border-radius: 100%;
    display: flex;
    height: 80px;
    justify-content: center;
    overflow: hidden;
    position: relative;
    width: 80px;
}

.Logo .image {
    align-items: center;
    display: flex;
}

.Logo .image img {
    width: 80px;
}
